.projects-title {
    font-family: 'Zilla Slab Highlight';
    color: blueviolet;
    font-size: 32px;
}

.project {
    display: flex;
    direction: row;
    justify-content: center;
    margin: 24px auto 24px auto;
    background-color: #161c20;
    border-radius: 4px;
    padding: 8px;
    max-width: 1048px;
}

.project-info {
    color: #d3d3d3;
    line-height: 24px;
}

.project-title {
    font-weight: bold;
    font-size: 24px;
}

.project-type {
    color: gray;
}

.project-video-container {
    display: flex;
    direction: row;
    justify-content: center;
}

.project-video {
    border: 1px solid #939393;
    border-radius: 4px;
    height: 200px;
    align-self: center;
    margin-right: 16px;
}

.project-build {
    color: #d3d3d3;
    line-height: 24px;
}

.project-button {
    margin: 8px auto 8px auto;
    background-color: blueviolet;
    border-color: #9370dc;
    &&:hover {
        background-color: #9370dc;
        border-color: blueviolet;
    }
}

